	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.davosnewbies.com/2005/06/07/where-the-us-comes-up-short/\" title=\"Davos Newbies: Where the US comes up short\">Davos Newbies: Where the US comes up short</a> \"In so many ways, American life is very easy (for those of us fortunate enough to have jobs and money). Things work smoothly and technology is generally advanced. That's to be expected in the richest nation on earth. What's not expected is the number of areas where US technology compares woefully to poor, benighted Britain: the structure of mobile telephony, the comparative underdevelopment of digital television, the absence of digital radio and congestion charging are the four things that spring immediately to mind.\"</p>","fields":{"title":"Where the US comes up short","date":"09 Jun 2005","url":"https://papascott.de/2005-06-09-where-the-us-comes-up-short/"}}},"pathContext":{"slug":"/2005-06-09-where-the-us-comes-up-short/","prev":{"fields":{"title":"Turning Germany into Delaware","date":"09 Jun 2005","slug":"/2005-06-09-turning-germany-into-delaware/"}},"next":{"fields":{"title":"Why Not Us?","date":"09 Jun 2005","slug":"/2005-06-09-why-not-us/"}}}}

